.Header__Header__oE2FH {
    position: fixed;
    display: flex;
    justify-content: space-between;
    padding: 1rem 2rem;
    top: 0;
    left: 0;
    z-index: 100;
    /* height: 3rem; */
    width: 100%;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}
.Header__Header__oE2FH span {
    padding-right: 0.5rem;
}

@media (max-width: 992px) {
    .Header__Header__oE2FH {
        display: block;
    }
    .Header__Header__oE2FH div {
        margin: 0.5rem 0;
    }       
}